import $ from 'jquery'

/**
 * *********************
 * Notifier.js - Developed by rlemon (rob.lemon@gmail.com) https://github.com/rlemon/Notifier.js
 * Licensed under GNU GPL V3 https://github.com/rlemon/Notifier.js/blob/master/LICENSE
 **********************
 */
export const notifier = (function () {

   const applyStyles = function (element, styleObject) {

      for (const prop in styleObject) {

         element.style[prop] = styleObject[prop]

      }

   }
   const fadeOut = function (element) {

      if (element.style.opacity && element.style.opacity > 0.05) {

         element.style.opacity = element.style.opacity - 0.05

      } else if (element.style.opacity && element.style.opacity <= 0.1) {

         if (element.parentNode) {

            element.parentNode.removeChild(element)

         }

      } else {

         element.style.opacity = 0.9

      }
      setTimeout(function () {

         fadeOut.apply(this, [element])

      }, 1000 / 30)

   }

   const config = {

      /* How long the notification stays visible */
      "default_timeout": 5000, // EB: default was 5000
      /* Container for the notifications */
      "container": document.createElement('div'),

      /* Container styles for notifications */
      "container_styles": {
         "position": 'fixed',
         "zIndex": 99999,
         "right": '12px',
         "top": '12px'
      },

      /* Individual notification box styles */
      "box_styles": {
         "cursor": 'pointer',
         "padding": '12px 18px',
         "margin": '0 0 6px 0',
         "backgroundColor": '#000',
         "opacity": 0.8,
         "color": '#fff',
         "font": 'normal 13px \'Lucida Sans Unicode\', \'Lucida Grande\', Verdana, Arial, Helvetica, sans-serif',
         "borderRadius": '3px',
         "boxShadow": '#999 0 0 12px',
         "width": '300px'
      },

      /* Individual notification box hover styles */
      "box_styles_hover": {
         "opacity": 1,
         "boxShadow": '#000 0 0 12px'
      },

      /* Notification title text styles */
      "title_styles": {"fontWeight": '700'},

      /* Notification body text styles */
      "text_styles": {
         "display": 'inline-block',
         "verticalAlign": 'middle',
         "width": '240px',
         "padding": '0 12px'
      },

      /* Notification icon styles */
      "icon_styles": {
         "display": 'inline-block',
         "verticalAlign": 'middle',
         "height": '36px',
         "width": '36px'
      }
   }

   applyStyles(config.container, config.container_styles)

   document.body.appendChild(config.container)

   return {
      notify(message, delay, title, image) {

         const notification = document.createElement('div')

         applyStyles(notification, config.box_styles)

         notification.onmouseover = function () {

            applyStyles(this, config.box_styles_hover)

         }
         notification.onmouseout = function () {

            applyStyles(this, config.box_styles)

         }
         notification.onclick = function () {

            this.style.display = 'none'

         }

         const icon = $('<img/>').attr('src', image)[0]

         applyStyles(icon, config.icon_styles)

         notification.appendChild(icon)

         const text = document.createElement('div')
         applyStyles(text, config.text_styles)

         notification.appendChild(text)

         if (title) {

            const titleText = $('<div/>')[0]
            applyStyles(titleText, config.title_styles)
            titleText.appendChild($('<span/>').html(title)[0])
            text.appendChild(titleText)

         }

         if (message) {

            const messageText = $('<div/>')[0]
            messageText.appendChild($('<span/>').html(message)[0])
            text.appendChild(messageText)

         }

         config.container.insertBefore(notification, config.container.firstChild)

         setTimeout(() => {

            fadeOut(notification)

         }, delay || config.default_timeout)

      },
      info(message, delay) {

         this.notify(message, delay, 'GTT Booster', 'data:image/png;base64,')

      },
      warning(message, delay) {

         this.notify(message, delay, 'GTT Booster', 'data:image/png;base64,')

      },
      success(message, delay) {

         this.notify(message, delay, 'GTT Booster', 'data:image/png;base64,')

      },
      error(message, delay) {

         this.notify(message, delay, 'GTT Booster', 'data:image/png;base64,')

      }
   }

}())



